body,
input,
textarea,
select,
button,
table {
    font-family: "나눔고딕", "NanumGothic", "NanumGothicWebFont", "Malgun Gothic", "맑은 고딕", "Dotum", "돋움", "Helvetica", "Apple SD Gothic Neo", "sans-serif";
    font-size: 12px;
    line-height: 1.2;
}

body {
  position: relative;
  -webkit-text-size-adjust: none;
  overflow-y: scroll;
}
:root {
  /*color*/
  --white-color: #fff;
  --black-color: #140a00;
  --blue-color: #045fd4;
  --red-color: #ff0000;
  --grey-dark-color: #909090;
  --grey-light-color: #e0e0e0;    
  /*Size*/
  --size-padding: 12px;
  --avater-size : 36px;

  /*Font Size*/
  --font-large:18px;
  --font-medium:14px;
  --font-small:12px;
  --font-micro:10px;

  --padding : 16px;
}

.App {
  text-align: center;  
  background-color: white;
  font-size: 25px;
  font-weight: bold;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
 
.div{
  box-sizing : border-box;
}

.black-nav {
  display: flex;
  background-color:#FFE0B2;
  width:100%; 
  padding-left :20px;
  color:  rgb(64 71 86);
}

.list {
  padding-left:20px;
  border-bottom : 1px solid grey; 
  font-size: var(--font-medium);
  text-align : left;
  color: var(--grey-dark-color);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
 